/* Generic slick-slider styles */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  user-select: none;
  touch-action: none;
  margin: 0; }

.slick-list:focus {
  outline: none; }

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0px;
  padding: 0px; }

.slick-slider .slick-list,
.slick-track,
.slick-slide,
.slick-slide img {
  transform: translate3d(0, 0, 0);
  height: 100%; }

.slick-track {
  position: relative;
  left: 0px;
  top: 0px;
  display: table; }

.slick-track:after {
  content: "";
  display: table;
  clear: both; }

.slick-slide {
  float: left;
  min-height: 1px; }

.slick-slide img {
  display: block; }

.slick-slide a {
  display: block; }

.slick-slide {
  display: none; }

.slick-list.dragging img {
  pointer-events: none; }

.slick-initialized .slick-track .slick-slide {
  display: table-cell; }

.ie7 .carousel .slick-slide {
  display: block; }

.carousel {
  margin-bottom: 56px; }

/* Carousel Controls */
.carousel .slick-arrow {
  -webkit-appearance: none;
  cursor: pointer;
  outline: none;
  border: none;
  padding: 0;
  margin: 0;
  font-size: 0;
  border-radius: 2px;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  z-index: 1;
  height: 40px;
  width: 40px;
  background: #fff;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  background-size: 60%;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
  transition: opacity 0.3s ease-in-out, box-shadow 0.3s ease-in-out, background 0.3s ease-in-out; }
