/* Looped Carousels */
.carousel--looped {
  width: 100%; }
  .carousel--looped .banner {
    position: relative;
    display: block; }
  .carousel--looped .slick-dots {
    display: inline-block;
    position: absolute;
    top: 100%;
    left: 50%;
    padding: 0;
    margin: 0;
    height: 56px;
    list-style-type: none;
    overflow: hidden; }
    .carousel--looped .slick-dots > li {
      float: left;
      position: relative;
      cursor: pointer;
      outline: none;
      padding: 0 14px;
      height: 56px;
      width: 16px; }
      .carousel--looped .slick-dots > li > button {
        -webkit-appearance: none;
        cursor: pointer;
        outline: none;
        border: none;
        background: none;
        font-size: 0;
        padding: 0;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        margin: auto;
        width: 10px;
        height: 10px; }
